import { Injectable } from '@angular/core';
import{HttpClient} from '@angular/common/http'
import { htmlAstToRender3Ast } from '@angular/compiler/src/render3/r3_template_transform';
import{ res} from '../tablacargas/tablacargas-datasource';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CargasservisService {

  constructor(private ht:HttpClient) { }

  public  findcargas(ano:string,mes:string,programa:string):Observable<res[]> {
   return this.ht.get<res[]>('http://localhost:8060/findcargas/'+ano+'/'+mes+'/'+programa);
     }

}
