import { AfterViewInit, Component, OnInit, ViewChild, AfterContentInit } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable } from '@angular/material/table';
import { TablacargasDataSource, TablacargasItem } from './tablacargas-datasource';
import { Input } from '@angular/core';
import{CargasservisService} from '../services/cargasservis.service';
import{Renderer} from '@angular/core'
import{HttpClient} from '@angular/common/http';
import { from } from 'rxjs';

@Component({
  selector: 'app-tablacargas',
  templateUrl: './tablacargas.component.html',
  styleUrls: ['./tablacargas.component.css']
})
  
 
export class TablacargasComponent implements AfterViewInit, OnInit,AfterContentInit {
  ngAfterContentInit(): void {
    this.devolvercargas();
  }
  @ViewChild(MatPaginator, {static: false}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: false}) sort: MatSort;
  @ViewChild(MatTable, {static: false}) table: MatTable<TablacargasItem>;
  @Input()
  dataSource: TablacargasDataSource;
  ANO:string='2019';
  mes:string='01';
  programa:string='HIS';

  constructor(private ca:CargasservisService,private ren:Renderer){}

  /** Columns displayed in the table. Columns IDs can be added, removed, or reordered. */
  displayedColumns = ['pun_dig', 'programa','ano','mes','nombre_pun_dig','cantidad'];

  ngOnInit() {
    this.dataSource = new TablacargasDataSource();
    
  }


  setDatos(dat:TablacargasDataSource){

  this.dataSource=dat;

    }


    public doFilterprograma = (value: string) => {
      
      this.programa=value;
      this.devolvercargas();
      
    }

    public doFilterano = (value: string) => {
      
      this.ANO=value;
      this.devolvercargas();
      
    }

    public doFiltermes = (value: string) => {
      this.mes=value;
      this.devolvercargas();
     
    }


  devolvercargas(){

  
      this.ca.findcargas(this.ANO,this.mes,this.programa).subscribe(  x=>{ console.log(`Observer tiene una respuesta del web Api: ${JSON.stringify(x)}`); 
      this.dataSource.data=x[0].root.pun_dig;
      this.dataSource.sort.sortChange.emit( this.dataSource.sort);
      
     
      },
      err => console.error('Observer tiene un error: ' + err),
      () =>{ console.log('Observer tiene una notificación completa');})
    
      }

  ngAfterViewInit() {

    
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
    this.table.dataSource = this.dataSource;
    this.devolvercargas();
    
  }
  ngOnChange(){
    this.devolvercargas();

  }
 
}
